/*mobile first*/
.Intro {
    background-color: rgb(252, 249, 255);
    height: auto;
    font-size: 15px;
}

.Profile {
    display: block;
}

.Pic {
    display: block;
    padding-bottom: 5px;
    padding-top: 10px;
}

.Pic .Picture {
    display: block;
    border-radius: 50%;
    padding: 0;
    height: 180px;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 5px;
}

.Pic .Email {
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    margin-top: 5px;
    margin-bottom: 0px;
    height: 15px;
}

.Info {
    display: block;
    height: 100%;
    font-family: 'Open Sans', sans-serif;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
}

.References {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 10px;
}

.Reference {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
}

.Logos {
    width: 40px;
}

.Icons {
    font-size: 15px;
}

/*medium screens (Ex. tablets)*/
@media (min-width: 768px) {
    .Intro {
        font-size: 17px;
    }
    
    .Profile {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: auto;
        padding-left: 10px;
        padding-right: 10px;
    }

    .Pic {
        padding-right: 10px;
        padding-bottom: 5px;;
    }

    .Pic .Picture {
        height: 230px;
    }

    .Pic .Email {
        margin-top: 20px;
        padding-bottom: 20px;
    }

    .Info {
        padding-left: 0px;
        width: 435px;
    }
    
    .Logos {
        width: 50px;
    }
    
    .Icons {
        font-size: 17px;
    }
}

/*large devices (Ex. large tablets, small laptops)*/
@media ( min-width: 991px) {
    .Intro {
        font-size: 17px;
    }

    .Profile {
        display: flex;
        justify-content: center;
        align-items: center;
        height: auto;
        padding-left: 10px;
        padding-right: 10px;
    }

    .Pic {
        padding-right: 40px;
        padding-bottom: 5px;;
        padding-top: 25px;
    }

    .Pic .Picture {
        height: 230px;
    }

    .Pic .Email {
        margin-top: 20px;
        padding-bottom: 20px;
    }

    .Info {
        padding-left: 0px;
        width: 490px;
    }
}

/*extra large devices (Ex. large desktops)*/
@media (min-width: 1440px) {
    .Profile {
        padding-left: 10px;
        padding-right: 10px;
    }

    .Pic {
        padding-right: 50px;
        padding-bottom: 5px;;
        padding-top: 30px;
    }

    .Pic .Email {
        margin-top: 20px;
        padding-bottom: 20px;
    }

    .Info {
        padding-left: 0px;
        width: 520px;
    }
    
    .References {
        margin-left: 200px;
        margin-right: 200px;
    }
}